import React from 'react';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Heart, X } from 'lucide-react';
import { MemeDisplayProps } from './types';

const MemeDisplay = ({ 
  imageUrl, 
  alt = "Meme", 
  onLike, 
  onDislike, 
  showActions = true 
}: MemeDisplayProps) => {
  return (
    <div className="relative">
      <Card className="overflow-hidden bg-black">
        <div className="aspect-square relative">
          <img 
            src={imageUrl} 
            alt={alt}
            className="w-full h-full object-cover"
          />
          
          {showActions && (
            <div className="absolute bottom-0 left-0 right-0 p-4 flex justify-center gap-4 bg-gradient-to-t from-black/50 to-transparent">
              <Button
                size="lg"
                variant="outline"
                className="rounded-full w-16 h-16 bg-white hover:bg-red-50 border-2 border-red-500"
                onClick={onDislike}
              >
                <X className="w-8 h-8 text-red-500" />
              </Button>
              
              <Button
                size="lg"
                variant="outline"
                className="rounded-full w-16 h-16 bg-white hover:bg-green-50 border-2 border-green-500"
                onClick={onLike}
              >
                <Heart className="w-8 h-8 text-green-500" />
              </Button>
            </div>
          )}
        </div>
      </Card>
    </div>
  );
};

export default MemeDisplay;